import React from "react"
import Header from "../components/header"
import { Link } from "gatsby"

const IndexPage = () => {
  return (
    <div className="wrapper">
      <Header />
      {/* <h1>Witaj na mojej stronie Gatsby!</h1> */}
      <main>
        <img src="/icons/IMG_3241.jpg" alt="Background" />
        <div class="overlay">
          <h1>
            Witamy na stronie<br></br> Hurtowni Materiałów Budowlanych
          </h1>
          <br></br>
          <div className="shopbtnmain">
            <p>klinkier to nasza specjalność...</p>
            <Link href=''>Sklep online</Link>
          </div>
        </div>
      </main>
      <section>JOŁ</section>
    </div>
  )
}

export default IndexPage
